import tTable from '@/components/t-table/t-table.vue'
import tTh from '@/components/t-table/t-th.vue'
import tTr from '@/components/t-table/t-tr.vue'
import tTd from '@/components/t-table/t-td.vue'

export default {
	components: {
	    tTable,
	    tTh,
	    tTr,
	    tTd
	},
	data() {
		return {
			fullHight:'',
			currentHight:'',
			showFlag:true,
			msg:'',
			list:[],
			uid:'',
			barcode:'',
			chuanghao:'',
			huohao:'',
			color:'',
			chima:'',
			yfsl:'',
			sssl:'',
			cp:'',
			cs:'',
			dk:''      //服务器ip
		}
	},
	onLoad: function(r) {
		this.barcode = r.result;
		this.dk = this.websiteUrl;
		uni.getStorage({
			key:'fwqsj',
			success: res => {
				this.dk = res.data
			}
		}),
		uni.getStorage({
			key: 'loginData',
			success: res => {
				this.uid = res.data.uid
			}
		})
		
		uni.request({
			url:`https://${this.dk}/getInfoSearch.aspx`,  //ES6模板字符串
			data:{Info: this.barcode},
			success: (res) => {	
				if(res.statusCode == 200){
					let temp = res.data.split(",")
					if(temp[0] == 0){
						this.msg = temp[1];
						this.barcode = this.barcode;
						this.chuanghao = temp[3];
						this.huohao = temp[4];
						this.color = temp[5];
						this.chima = temp[6];
						this.yfsl = temp[7];
						this.sssl = temp[8];
						this.cp = temp[9];
						this.cs = temp[10];
					}else{
						this.msg = temp[1];
						this.chuanghao = '';
						this.huohao = '';
						this.color = '';
						this.chima = '';
						this.yfsl = '';
						this.cp = '';
						this.cs = '';
					}
				}else{
					this.msg = "无效条码";
					this.chuanghao = '';
					this.huohao = '';
					this.color = '';
					this.chima = '';
					this.yfsl = '';
					this.cp = '';
					this.cs = '';
					this.sssl = '';
				}
			}				
		});
		
		uni.request({
			url: `https://${this.dk}/getSearchWorktype.aspx`,
			method: 'GET',
			data: {
				userid:this.barcode
			},
			success: res => {
				if(res.statusCode == 200){
					console.log(JSON.stringify(res));
					this.list = res.data;
				}
			}
		})
	},
	onBackPress(options) {  
		if (options.from === 'navigateBack') {
			uni.offWindowResize(() => {
			    console.log('取消监听窗口尺寸变化事件')
			});
			uni.switchTab({
				url: '/pages/index/index'
			});
			return true; 
	    }  
	    return false;  
	}, 
	methods: {
		change(){
			//console.log('HELLO');
			this.cs = this.yfsl - this.sssl - this.cp;   
		},
		test(){
			this.barcode = '';
		},
		scanCode(){
			uni.scanCode({	
				onlyFromCamera: true,
				scanType:['qrCode','barCode'],   //这里有个坑，不写的话扫描速度有点慢
				success: (res) => {
					//console.log(res);
					this.barcode = res.result;
					uni.request({
						url:`https://${this.dk}/getInfoSearch.aspx`,  //ES6模板字符串
						data:{Info: this.barcode},
						success: (res) => {	
						    //console.info("@@@@"+JSON.stringify(res));	
							if(res.statusCode == 200){
								let temp = res.data.split(",")
								if(temp[0] == 0){
									this.msg = temp[1];
									this.barcode = this.barcode;
									this.chuanghao = temp[3];
									this.huohao = temp[4];
									this.color = temp[5];
									this.chima = temp[6];
									this.yfsl = temp[7];
									this.sssl = temp[8];
									this.cp = temp[9];
									this.cs = temp[10];
								}else{
									this.msg = temp[1];
									this.chuanghao = '';
									this.huohao = '';
									this.color = '';
									this.chima = '';
									this.yfsl = '';
									this.cp = '';
									this.cs = '';
									this.sssl = '';
								}
							}else{
								this.msg = "无效条码";
								this.chuanghao = '';
								this.huohao = '';
								this.color = '';
								this.chima = '';
								this.yfsl = '';
								this.cp = '';
								this.cs = '';
							}
						}				
					});
					
					uni.request({
						url: `https://${this.dk}/getSearchWorktype.aspx`,
						method: 'GET',
						data: {
							userid:this.barcode
						},
						success: res => {
							if(res.statusCode == 200){
								console.log(JSON.stringify(res));
								this.list = res.data;
							}
						}
					})
					
				}
			})
		},
		okbtn(){                    //按钮重复点击处理问题
			uni.request({
				url:`https://${this.dk}/updatesssl.aspx`,  //ES6模板字符串
				data:{
					Info:this.barcode,
					sssl:this.sssl,
					Cp:this.cp,
					Cs:this.cs,
					UID:this.uid
				},
				success: (res) => {	
				    console.log("*****"+JSON.stringify(res));
					if(res.statusCode == 200){
						let temp = res.data.split(",")
						if(temp[0] == 0){
							this.msg = '修改成功'
							this.barcode ='';
							this.chuanghao = '';
							this.huohao = '';
							this.color = '';
							this.chima = '';
							this.yfsl = '';
							this.sssl = '';
							this.cp = '';
							this.cs = '';
							uni.showToast({
								position:'top',
								title:'修改成功',
								icon:'none',
								duration: 3000
							})
						}else{
							uni.showToast({
								position:'top',
								title:temp[1],
								icon:'none',
								duration: 2000
							});
						}
					}else{
						uni.showToast({
							title:'网络繁忙，请稍后重试',
							icon:'none',
							duration: 2000
						});
					}
				}				
			})
		}
	}
}
